import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import './App.css';
import { Navigation } from './components/navigation'
import { Layout } from './components/layout'
import { Home } from './components/sections/home'
import { About } from './components/sections/about'
import { Contact } from './components/sections/contact'
import { FinancialModel } from './components/sections/financial-model'
import { MedicalServices } from './components/sections/medical-services'
import { Team } from './components/sections/team'
import { Vision } from './components/sections/vision'
import { Error } from './components/sections/error'
import { Footer } from './components/footer'

class App extends Component {
  render () {
    return (
      <>
        <Router>
          <Navigation />
          <Layout>
            <Switch>
              <Route exact path='/' component={Home} />
              <Route path='/about' component={About} />
              <Route path='/contact' component={Contact} />
              <Route path='/financial-model' component={FinancialModel} />
              <Route path='/medical-services' component={MedicalServices} />
              <Route path='/team' component={Team} />
              <Route path='/vision' component={Vision} />
              <Route component={Error} />
            </Switch>
          </Layout>
          <Footer />
        </Router>
      </>
    );
  }
}

export default App;
