import React from 'react'
import { Link } from 'react-router-dom'
import { Navbar, Nav } from 'react-bootstrap'
import styled from 'styled-components'
import { withRouter } from "react-router"
import logo from '../assets/Logo.png'

const Styles =  styled.div`
  .navbar {
    background-color: #ffffff;
    box-shadow: 1px 1px 4px #145c97;
    .nav-item {
      margin-left: 15px;
    }
    .navbar-brand img{
      height: 60px;
      width: auto;
      margin-top: -10px;
      margin-bottom: -10px;
    }
  }
  a, .navbar-brand, .navbar-nav .nav-link {
    color: #145c97;
    font-size: 14px;

    &:hover {
      color: #145c97;
      text-decoration: none;
    }
  }
  .active {
    background-color: #145c97;
    transition: width 2s;
    a {
      color: #ffffff;
      font-weight: 500;
    }
  }
`

const Header = (props) => {
  const { location } = props
  return (
    <Styles>
      <Navbar collapseOnSelect expand="sm" fixed="top">
        <Navbar.Brand href="/">
          <img src={logo} alt="logo" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Item className={location.pathname === '/' ? 'active' : ''}>
              <Nav.Link>
                <Link to="/">Home</Link>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className={location.pathname === '/medical-services' ? 'active' : ''}>
              <Nav.Link>
                <Link to="/medical-services">Medical Services</Link>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className={location.pathname === '/financial-model' ? 'active' : ''}>
              <Nav.Link>
                <Link to="/financial-model">Financial Model</Link>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className={location.pathname === '/team' ? 'active' : ''}>
              <Nav.Link>
                <Link to="/team">Team</Link>
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </Styles>
  )
}

export const Navigation = withRouter(Header)